<template>
  <div class="audit_merge">
    <listSearch 
      v-model="searchFormData"
      @searchConfirm="goSearch"
      :searchArr="searchArr">
    </listSearch>
    <div class="content">
      <div class="table_box mgt20">
        <Table ref="selectionTable" 
          :columns="columns"
          :data="bodyList">
          <template slot-scope="{ row }" slot="name">
            <span>{{ row.name }}</span>
          </template>
          <template slot-scope="{ row }" slot="age">
            <span>{{ row.name }}</span>
          </template>
          <template slot-scope="{ row }" slot="address">
            <span>{{ row.name }}</span>
          </template>
          <template slot="action">
            <div>
              <Button type="primary" @click="btnOperate(1)">指定仲裁员</Button>
            </div>
          </template>
        </Table>
        <!-- 分页 -->
        <Page class="mgt20" :total="pageObj.total"
          :page-size="pageObj.pageSize"
          :page-size-opts="pageObj.pageSizeOptions" 
          show-elevator
          show-sizer 
          @on-page-size-change="pageSizeChange"
          @on-change="pageNumChange"/>
      </div>
    </div>
    <!-- 弹窗部分 -->
    <popupBox :show="popupShow" 
      :titleText="popupTypeObj[popupType]['title']"
      :width="popupTypeObj[popupType]['width']"
      @popupConfirm="popupConfirm" 
      @popupCancel="popupCancel"
      >
      <!-- 仲裁员 -->
      <div class="select_content" v-if="popupType == 1">
        <div class="header_box">
          <div class="title">选择仲裁员</div>
            <Input
              prefix="ios-search"
              v-model="selectPerson"
              placeholder="姓名或首字母"
              style="width:200px"
            />
        </div>
        <Table  class="file_table" :columns="personColumns" :data="personTable">
          <template slot-scope="{ row,index}" slot="action">
            <Checkbox v-model="row.checked" @on-change="handleCheckbox(row,index)"></Checkbox>
          </template>
        </Table>
        <Page :current="1" :total="10" :page-size="3" />
      </div>
    </popupBox>
  </div>
</template>

<script>
import listSearch from '@/components/list-search/search.vue'
import expandRow from '../expand_row'
import mixin from '../mixin'
// import zzlist from '@/components/zzlist/zzlist'
export default {
  name: 'audit_merge',
  components: { listSearch },
  mixins: [mixin],
  data () {
    return {
      popupShow: false,
      popupType: 1,
      popupTypeObj: {
        1: {type: 1, title: '指定仲裁员', width: '740'}
      },
      selectPerson:'', //选择仲裁员
      searchFormData: {
        personalName: null,
        companyName: null,
        checkbox: [],
        radio: null,
        status: null
      },
      searchArr: [
        {type: 'input', value: 'companyName', label: '案号', placeholder: '请输入企业名称'},
        {type: 'select', value: 'status', label: '审核状态',
         arr: [
          {label: '通过', value: 1},
          {label: '未通过', value: 2}
         ], 
          placeholder: '请选择'
        }
      ],
      columns: [
        {
          type: 'expand',
          width: '20',
          render: (h, params) => {
            return h(expandRow, {
              props: {
                  row: params.row
              }
            })
          }
        },
        {
          title: '标题',
          slot: 'name'
        },
        {
          title: '申请时间',
          slot: 'age'
        },
        {
          title: '开庭时间',
          slot: 'age'
        },
        {
          title: '案件状态',
          slot: 'age'
        },
        {
          title: '操作',
          slot: 'action'
        }
      ],
      bodyList: [
        {
          name: '王小明',
          age: 18,
          birthday: '919526400000',
          address: '北京市朝阳区芍药居'
        },
        {
          name: '张小刚',
          age: 25,
          birthday: '696096000000',
          address: '北京市海淀区西二旗'
        }
      ],
      personColumns:[{title:'姓名',key:"name"},{title:"专业",key:"name"},{title:"选择",slot:"action"}],
      personTable:[{name:'张三1',checked:false},{name:'张三2',checked:false}]
    }
  },
  mounted () {
    this.getList()
  },
  methods: {
    // 搜索
    goSearch () {
      console.log(this.searchFormData)
    },
    // 按钮此作
    btnOperate (type) {
      // 1:驳回原因
      console.log(type)
      this.popupType = type
      this.popupShow = true
    },
    // 弹窗确认
    popupConfirm () {
      console.log(1)
    },
    // 弹窗取消
    popupCancel () {
      console.log(1)
      this.popupShow = false
    },
    // 单选
    handleCheckbox (row, index) {
      let copy = JSON.parse(JSON.stringify(this.personTable));
      if(row.checked){
        copy.map(item=>{
          item.checked=false
        })
        copy[index]=row;
        this.personTable=copy;
      }
    },
    // 请求demo
    getList () {
      this.commonApi.demo()
        .then(res => {
          console.log(res)
        })
        .catch(err => {
          console.log(err)
        })
    }
  }
}
</script>
<style lang="less">
.audit_merge {
  .content {
    margin-top: 30px;
    padding: 20px 10px;
    background: #fff;
    border-radius:4px;
    .buttons {
      padding-right: 20px;
      text-align: right;
    }
    .table_box {
      // padding: 0 2px;
    }
  }
  .mgt20 {
    margin-top: 20px;
  }
  .mgl10 {
    margin-left: 10px;
  }
}
.select_content {
  .header_box {
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
  .file_table{
    margin: 10px 0 ;
  }
}
</style>
<style lang="less">
@import url('../../../style/iview_table.less');
</style>